.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 90%;
  &GameContainer {
    border: 1px solid var(--blue);
    border-radius: 10px;
    overflow: hidden; }
  &Nicknames {
    width: 60%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px; }
  &Nickname {
    font-size: 18px;
    font-weight: bold; } }

.orderText {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 5px; }
